import React from 'react'
import './InputMobile.scss'

const InputMobile = ({ content, type, textarea, action, capitalize, value }) => {




    return (
        <div className={textarea ? "wiersz_mobile textarea" : "wiersz_mobile"}>
            <input style={{ textTransform: capitalize ? "capitalize" : "none" }} onChange={(e) => action(e.target.value)} type={type} value={value} />
            <label className='label_mobile' htmlFor="">{content}</label>

        </div>
    )
}

export default InputMobile